@import 'mathsass/dist/_math';
@import 'modularscale-sass/stylesheets/modularscale';
@import 'build/sass/functions';
@import 'build/sass/variables';
@import 'bootstrap/scss/mixins';
@import 'bootstrap/scss/utilities/text';
@import 'bootstrap/scss/utilities/display';
@import 'editor/_*.scss';

@font-face {
    font-family: 'Metropolis';
    font-style: normal;
    font-weight: 300;
    src: local('Metropolis-Light');
}

@font-face {
    font-family: 'Metropolis';
    font-style: italic;
    font-weight: 300;
    src: local('Metropolis-LightItalic');
}

@font-face {
    font-family: 'Metropolis';
    font-style: normal;
    font-weight: 600;
    src: local('Metropolis-SemiBold');
}

@font-face {
    font-family: 'Metropolis';
    font-style: italic;
    font-weight: 600;
    src: local('Metropolis-SemiBoldItalic');
}

@font-face {
    font-family: 'Metropolis';
    font-style: normal;
    font-weight: 800;
    src: local('Metropolis-ExtraBold');
}

@font-face {
    font-family: 'Metropolis';
    font-style: italic;
    font-weight: 800;
    src: local('Metropolis-ExtraBoldItalic');
}

html,
body {
    font-family: 'Metropolis';
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility !important;
    -webkit-font-smoothing: antialiased !important;
    font-smoothing: antialiased !important;
}
